import React from "react";
import {
	View,
	Pressable,
	Text,
	ScrollView,
	StyleSheet,
	Dimensions,
} from "react-native";
import { Home, BriefcaseBusiness, Phone } from "lucide-react";
import { colors } from "../themes";
import UserProfile from "./UserProfile";
import WorkExperience from "./WorkExperience";
import Contact from "./Contact";

const { width, height } = Dimensions.get("window");
const tabs = [
	{
		Icon: Home,
		title: "Home",
	},
	{
		Icon: BriefcaseBusiness,
		title: "Work",
	},
	{
		Icon: Phone,
		title: "Contact",
	},
];
const Desktop = () => {
	const [currentTab, setCurrentTab] = React.useState(0);

	const renderTab = () => {
		switch (currentTab) {
			case 0:
				return <UserProfile />;
			case 1:
				return <WorkExperience />;
			default:
				return <Contact />;
		}
	};

	return (
		<View style={style.main}>
			<ScrollView showsVerticalScrollIndicator={false}>
				{renderTab()}
			</ScrollView>
			<View style={style.footer}>
				{tabs.map((Tab, idx) => {
					const selected = currentTab === idx;
					const color = selected ? colors.text : colors.greyText;
					return (
						<Pressable
							onPress={() => setCurrentTab(idx)}
							style={style.tab}
						>
							<Tab.Icon size={20} color={color} />
							<Text style={[style.tabText, { color }]}>{Tab.title}</Text>
						</Pressable>
					);
				})}
			</View>
		</View>
	);
};

const style = StyleSheet.create({
	main: {
		width,
		height,
		backgroundColor: colors.bgColor,
	},
	footer: {
		backgroundColor: colors.bgColor,
		width,
		height: 50,
		borderTopWidth: 1,
		borderColor: colors.border,
		flexDirection: "row",
	},
	tab: {
		width: "33.3%",
		height: 50,
		justifyContent: "center",
		alignItems: "center",
	},
	tabText: {
		marginTop: 2,
		fontSize: 14,
		color: colors.greyText,
	},
});

export default Desktop;