import React from "react";
import { View, Text, Pressable, StyleSheet } from "react-native";
import { ChevronDown } from "lucide-react";
import { colors } from "../themes";

const JobCard = ({ data, onPress, selected }) => {
	const color = selected ? colors.primary : colors.greyText;
	return (
		<Pressable onPress={onPress} style={mStyle.main}>
			<View
				style={[style.header, { borderBottomWidth: selected ? 1 : 0 }]}
			>
				<Text style={[style.title, { color }]}>{data.title}</Text>
				<Text style={style.duration}>{data.duration}</Text>
			</View>
			{selected ? (
				<>
					<Text style={style.body}>{data.summary}</Text>
					<Text style={style.tag}>{data.tag}</Text>
				</>
			) : (
				<View style={style.arrow}>
					<ChevronDown size={20} color={colors.greyText} />
				</View>
			)}
		</Pressable>
	);
};

const mStyle = {
	main: {
		width: "95%",
		borderWidth: 1,
		borderRadius: 10,
		borderColor: colors.border,
		alignSelf: "center",
		marginTop: 30,
		outline: 'none',
		background: "radial-gradient(circle at 1e+02% -1e-15%, #232323 0%, #000000 5%)"
	}
}
const style = StyleSheet.create({
	header: {
		height: 70,
		width: "100%",
		justifyContent: "center",
		borderBottomWidth: 1,
		paddingHorizontal: 10,
		borderColor: colors.border,
	},
	title: {
		fontSize: 16,
		fontWeight: "600",
	},
	body: {
		paddingHorizontal: 10,
		marginVertical: 15,
		fontSize: 14,
		color: colors.greyText,
	},
	duration: {
		marginTop: 2,
		fontSize: 14,
		color: colors.greyText,
	},
	tag: {
		position: "absolute",
		top: 10,
		right: 10,
		borderRadius: 60,
		backgroundColor: colors.greyCard,
		color: colors.primary,
		fontWeight: "600",
		fontSize: 12,
		paddingHorizontal: 10,
		paddingVertical: 4,
	},
	arrow: {
		position: "absolute",
		top: 25,
		right: 20,
	},
});

export default JobCard;