import React from "react";
import { View, ScrollView, Text, StyleSheet, Dimensions } from "react-native";
import JobCard from "./JobCard";
import { colors } from "../themes";
import { PROFILE_WIDTH, CONTACT_WIDTH } from "./constants";

const jobs = require("../assets/jobs.json");
const { width, height } = Dimensions.get("window");

const WorkExperience = () => {
	const [currentId, setCurrentId] = React.useState(jobs[0].id);
	const renderJob = (job) => {
		const selected = currentId === job.id;
		return (
			<JobCard
				selected={selected}
				onPress={() => {
					if (selected) {
						setCurrentId(-1);
					} else {
						setCurrentId(job.id);
					}
				}}
				data={job}
				key={job.id}
			/>
		);
	};
	return (
		<View style={style.main}>
			<View style={style.header}>
				<Text style={style.title}>Work Experience</Text>
			</View>
			<div style={mStyle.headBorder} />
			<ScrollView showsVerticalScrollIndicator={false}>
				{jobs.map(renderJob)}
				<View style={{ height: 50, width: 100 }} />
			</ScrollView>
		</View>
	);
};

const style = StyleSheet.create({
	main: {
		height,
		width: "100%",
		paddingBottom: 50,
		backgroundColor: colors.card,
	},
	header: {
		width: "100%",
		height: 50,
		justifyContent: "center",
	},
	title: {
		textAlign: "center",
		fontSize: 18,
		color: colors.text,
		fontWeight: "bold",
	},
});

const mStyle = {
	headBorder: {
		width: "100%",
		height: 1,		
		background:
			"radial-gradient(circle at 50% 0%, #515050 0%, #000000 100%)",
	},
};

export default WorkExperience;